/**
* @Author: duxin
* description: 文章页面
*/

<template>
  <div class="article-wrap" v-html="mdContent">
  </div>
</template>

<script>
  import marked from "marked"
  import {fileRead} from "../../util/request"

  export default {
    data() {
      return {
        mdContent: ""
      }
    },
    mounted() {
      marked.setOptions({
        renderer: new marked.Renderer(),
        gfm: true,
        tables: true,
        breaks: false,
        pedantic: false,
        sanitize: false,
        smartLists: true,
        smartypants: false
      });
      this.init()
    },
    methods: {
      init() {
        let path = this.$route.query.mdPath;
        fileRead(path).then(res => {
          this.mdContent = marked(res)
        });

      }
    }
  }
</script>

<style scoped>
  .article-wrap {
    border-radius: 4px;
    padding: 15px 25px;
    background: rgba(206, 211, 200, 0.84)
  }
</style>



